
.commentsContainer {

    padding: 15px;
    width: 100%;
    background-color:rgba(216, 198, 160, 0.055);
}

.commentsContainer .comments {
    margin-bottom: 10px;
    margin-left: 5%;
    margin-right: 5%;
}

.commentsContainer .comment {
    
    width: auto;
    border-radius: 10px;
    padding: 5px;
}

.commentsContainer .authorSection {
    display: flex;
}

.commentsContainer .author {
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    margin-left: 10px;
    font-size: 90%;
}

.commentsContainer .content {
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 80%;
    padding: 5px;
}



.commentsContainer .newComment {
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 13px;
    display: flex;
    padding: 10px;
    border-radius: 15px;
    width: 90%;
    background-color: #ffdab0c2;
    word-break: break-all;

    height: 45px;
    margin-left: auto;
    margin-right: auto;
}

.commentsContainer .text {
    all: unset;
    width: 100%;
    color: rgba(0, 0, 0, 0.753);
}

.commentsContainer .sendButton {
    color: #704a1ec2;
}