.navbar2 {
  background: linear-gradient(90deg, rgb(47, 56, 55) 0%, rgb(23, 26, 25) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.Informado {
  font-size: 25px;
  background: -webkit-linear-gradient(rgb(119, 0, 119) 0%, rgb(255, 166, 0) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.navbar-container2 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo2 {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo32 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu2 {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item2 {
  height: 80px;
}

.nav-links2 {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  font-family: Arial;
}

.nav-links2:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars2 {
  color: #fff;
}

.nav-links-mobile2 {
  display: none;
}

.menu-icon2 {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems2 {
    position: relative;
  }

  .nav-menu2 {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active2 {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links2 {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links2:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo2 {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon2 {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times2 {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile2 {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile2:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
