/* */

.postCommentContainer {
    
    width: 100%;
    margin: 10px auto 10px auto;
}

.postContainer {
    display: flex;
    background-color: white;
    border: 2px solid #ffdab0;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;

    cursor: pointer;    
}

.postContainer:hover {
    box-shadow: 3px 3px #f1f1f1;
}

/* ---------------  POST SIDE  ---------------------------- */

.postSide {
    display: flex;
    flex-direction: column;

    background-color: #ffdab0;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    padding: 12px 8px;

    @media (max-width: 425px) {
    display: none;
  }
}

.postSide .arrow {
    color: rgb(255, 255, 255);
}

.postSide .arrow:hover {
    color: rgb(255, 243, 233);
}

.postSide .postCount {
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    margin: 5px auto 5px auto;
    color: rgb(26, 26, 27);
}

/* ---------------  POST MAIN  ---------------------------- */

.postMain {
    display: 'flex';
    flex-direction: 'column';
    width: 100%;
    padding: 10px;
}

.postMain .tagsContainer {
    display: flex;
    margin-left: auto;
}

.postMain .tag {
    margin-right: 5px;
    padding: 3px 10px 3px 10px;
    border-radius: 20px;
    color: rgb(195, 0, 114);
    border: 1px solid rgb(195, 0, 114);
}

.postMain .tag:hover {
    background-color: rgb(195, 0, 114);
    color: white;
}

.postMain .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 8px;
}

.postMain .userName{
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    color: rgb(120, 124, 126);
}

.postMain .footer{
    display: flex;
    align-items: center;
    width: 100%;
    padding: 8px;
}

.postMain .footerOptions{
    display: flex;
    align-items: center;
    margin-right: 10px;
    padding: 3px;
}

.postMain .footerOptions:hover{
    background-color: rgb(231 231 231);
    border-radius: 15px;
}

.postMain .content{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 8px;
}

.postMain .contentText{
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
}

.postMain .textOptions{
    font-family: 'IBM Plex Sans', Arial, sans-serif;
    font-size: 12px;
    line-height: 12px;
    font-weight: 700;
    color: #878a8c;
}
