* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

/*---------------------------------         LogIn/Register/RecoverPass Page          ------------------------------------- */

.LoginBackground {
  background: rgb(255, 255, 255);
  background: linear-gradient(0deg, rgb(200, 200, 200) 0%, rgb(255, 255, 255) 90%);
  height: 969px !important;
}

.LoginWrapper {
  display: flex;
  justify-content: center;
  margin-top: 180px;
}

.Login {
  width: 24%;
  padding: 30px;
  background: rgb(255, 255, 255);
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex !important;
  flex-direction: column !important;
  justify-content: space-between !important;
}

.LoginLeft {
  padding-top: 130px;
  padding-right: 100px;
  font-family: Calibri;
}

.LoginLeftTitle {
  font-size: 50px;
  background: -webkit-linear-gradient(rgb(119, 0, 119) 0%, rgb(255, 166, 0) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.RecoverPassTitle {
  font-size: 25px;
  text-align: center;
  color: rgb(105, 105, 105);
  font-family: Arial;
  font-weight: bold;
}

.recover-pass-sent-email {
  font-size: 25px;
  text-align: center;
  color: rgb(105, 105, 105);
  font-family: Arial;
  font-weight: bold;
}

.RecoverPass {
  width: 24%;
  padding: 30px;
  background: rgb(255, 255, 255);
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex !important;
  flex-direction: column !important;
  justify-content: space-between !important;
  margin-top: 75px;
}

.LoginLeftText {
  font-size: 20px;  
}

.loginInput{
  height: 50px;
  border-radius: 10px !important;
  border: 1px solid gray;
  margin-top: 15px;
  padding-left: 20px !important;
}

.loginButton{
  height: 50px;
  width: 100%;
  margin-top: 10px;
  padding-top: 6px !important;
  border-radius: 10px !important;
}

.loginForgot{
  text-align: center;
  margin-top: 10px;
  width: 100%;
  border-radius: 10px !important;
  height: 50px;
  padding-top: 12px !important;
  background-color: rgb(255, 255, 255) !important;
  border: solid 1px #cd83ff !important;
  color: #7200be !important;
}

.loginForgot:hover{
  text-align: center;
  background-color: #9145b4 !important;
  color: white !important;
}

.loginRegisterButton{
  width: 100%;
  align-self: center;
  margin-top: 10px;
  height: 50px !important;
  border-radius: 10px !important;
  border: none !important;
  background-color: #d84f00 !important;
  color: white;
  font-size: 20px;
  font-weight: 500;
  padding-top: 12px !important;
  cursor: pointer;
}

.loginRegisterButton:hover{
  background-color: #e06721 !important;
}

.alreadyHaveAccount{
  width: 100%;
  align-self: center;
  margin-top: 10px;
  height: 50px !important;
  border-radius: 10px !important;
  border: solid 1px #d84f00 !important;
  background-color: white !important;
  color: #d84f00 !important;
  font-size: 20px;
  font-weight: 500;
  padding-top: 12px !important;
  cursor: pointer;
}

.alreadyHaveAccount:hover{
  background-color: #e06721 !important;
  color: white !important;
}

.errorMessage {
  margin-left: 5px;
  font-size: 12px;
  font-weight: bold;
  font-family: Arial;
  color: red;
}

/*---------------------------------          FEED           ------------------------------------- */

.feed-search-bar {
  display: flex;
  margin: auto;
  margin-top: 15px;

  padding: 10px;
  border-radius: 30px;
  width: 20%;
}